import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { Redireccion } from '../../models/redireccion.model';


@Component({
  selector: 'app-redirecciones',
  templateUrl: './redirecciones.component.html',
  styleUrls: ['./redirecciones.component.css']
})
export class RedireccionesComponent implements OnInit {

  @ViewChild('closeModal', { static: false }) closeModal: ElementRef;

  redirecciones:object;
  redireccion:Redireccion = {origen:'', destino:''};
  selectedPath:string;

  constructor( private httpService:HttpService) { }

  ngOnInit() {

    this.httpService.get('redirecciones').subscribe(
      res => {
        this.redirecciones = res;                
      },
      error => {
        console.error(error);        
      });
  }

  saveRedireccion(){
    this.httpService.set('redirecciones', this.redirecciones).subscribe(
      res => {
        console.log(res);        
      },
      error => {
        console.error(error);        
      });
  }


  addPath(){
    this.selectedPath = '';
    this.redireccion = {
      origen:'',
      destino:''
    }
  }

  selectPath(entry){
    this.selectedPath = entry.key;
    this.redireccion = {
      origen:entry.key,
      destino:entry.value
    }
  }

  delete(){
    if(confirm('¿Estas seguro de borrar la ruta?')){
      delete this.redirecciones[this.selectedPath];
      this.redirecciones = {...this.redirecciones};
      this.saveRedireccion();
    }
  }

  save(){
    if(this.redireccion.origen.length == 0 || this.redireccion.destino.length == 0){
      alert('La ruta no es válida');      
    }else{      
      delete this.redirecciones[this.selectedPath];
      this.redirecciones[this.redireccion.origen] = this.redireccion.destino;
      this.redirecciones = {...this.redirecciones};
      this.closeModal.nativeElement.click();
      this.saveRedireccion();
    }
  }

  

}
