import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { Variable } from '../../models/variable.model';


@Component({
  selector: 'app-variables',
  templateUrl: './variables.component.html',
  styleUrls: ['./variables.component.css']
})
export class VariablesComponent implements OnInit {

  @ViewChild('closeModal', { static: false }) closeModal: ElementRef;

  variables:object;
  variable:Variable = {nombre:'', valor:''};
  selectedPath:string;

  constructor( private httpService:HttpService) { }

  ngOnInit() {

    this.httpService.get('variables').subscribe(
      res => {
        this.variables = res;                
      },
      error => {
        console.error(error);        
      });
  }

  saveRedireccion(){
    this.httpService.set('variables', this.variables).subscribe(
      res => {
        console.log(res);        
      },
      error => {
        console.error(error);        
      });
  }


  addPath(){
    this.selectedPath = '';
    this.variable = {
      nombre:'',
      valor:''
    }
  }

  selectPath(entry){
    this.selectedPath = entry.key;
    this.variable = {
      nombre:entry.key,
      valor:entry.value
    }
  }

  delete(){
    if(confirm('¿Estas seguro de borrar la variable?')){
      delete this.variables[this.selectedPath];
      this.variables = {...this.variables};
      this.saveRedireccion();
    }
  }

  save(){
    if(this.variable.nombre.length == 0 || this.variable.valor.length == 0){
      alert('La ruta no es válida');      
    }else{      
      delete this.variables[this.selectedPath];
      this.variables[this.variable.nombre] = this.variable.valor;
      this.variables = {...this.variables};
      this.closeModal.nativeElement.click();
      this.saveRedireccion();
    }
  }

  

}
