import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { Ruta } from '../../models/ruta.model';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
 
  @ViewChild('closeModal', { static: false }) closeModal: ElementRef;

  rutas:{};
  ruta:Ruta = {
    plantilla:'',
    sitemap_changefreq:'',
    sitemap_priority:0,
    parametros:{}
  };
  path:string;
  selectedPath:string;
  frequencies:string[]=['monthly', 'weekly'];
  templates:string[]=[];
  params:object[];


  constructor( private httpService:HttpService) { }
  ngOnInit() {
    this.httpService.templates().subscribe(
      res => {
        this.templates = Object.values(res);                
      },
      error => {
        console.error(error);        
      });
    this.httpService.get('rutas').subscribe(
      res => {
        this.rutas = res;                
      },
      error => {
        console.error(error);        
      });
  }

  saveRutas(){
    this.httpService.set('rutas', this.rutas).subscribe(
      res => {
        console.log(res);        
      },
      error => {
        console.error(error);        
      });
  }

  selectPath(ruta){
    this.ruta = {...ruta.value};
    let params = [];
    Object.keys(ruta.value.parametros).map(function(key, index) {
        params.push({
          name:key,
          value:ruta.value.parametros[key]
        })
    });
    this.params = params;   
    this.path = ruta.key;
    this.selectedPath = ruta.key;
  }

  save(){
    if(this.path.length == 0 || this.path[0] != '/'){
      alert('La ruta no es válida');      
    }else{
      let params = {};
      this.ruta.parametros = {};    
      this.params.forEach(param => {
        let paramArray = Object.values(param);
        if(paramArray[0].toString().length > 0){
          params[paramArray[0].toString()] = paramArray[1];       
        }
      });
      this.ruta.parametros = params;
      delete this.rutas[this.selectedPath];
      this.rutas[this.path] = this.ruta;
      this.rutas = {...this.rutas};
      this.closeModal.nativeElement.click();
      this.saveRutas();
    }
  }

  delete(){
    if(confirm('¿Estas seguro de borrar la ruta?')){
      delete this.rutas[this.selectedPath];
      this.rutas = {...this.rutas};
      this.saveRutas();
    }

  }

  addPath(){
    this.path = '';
    this.selectedPath = '';
    this.ruta = new Ruta;
    this.params = [];    
  }

  removeParam(i){
    this.params.splice(i, 1);
  }

  addParam(){
    this.params.push({
      name:'',
      value:''
    });
  }

}
